<script>
import {Skill, config} from "@/model/skill-sim";

const attrLayout = [
  {
    title: '属性',
    cols: [ '自身攻击', '自身防御', '自身轻功', '战场攻击', '战场防御', '战场轻功', '行动时序', '敌方攻击', '敌方防御', '敌方轻功', '敌方时序' ]
  },
  {
    title: '特性',
    cols: [ '移动距离', '攻击距离', '杀伤范围', '暴击', '双击', '出招', '主系出招']
  },
  {
    title: '攻击效果',
    cols: [ '威力', '命中', '伤害', '加受伤度', '主系威力', '主系命中', '主系伤害', '吸血' ]
  },
  {
    title: '防御效果',
    cols: [ '减威', '闪避', '减伤', '减受伤度', '主系减威', '主系闪避', '主系减伤', '反震', '反弹', '挪移'  ]
  },
  {
    title: '异常',
    cols: [ '异常', '异常概率', '异常回合' ]
  },
  {
    title: '免疫',
    cols: [ '免疫']
  },
]

export default {
  name: "CustomSimulatorView",
  data() {
    return {
      bottom: false,
      skill: new Skill(),
      grade: {
        desc: '',
        detail: () => '',
      },
      totalGrade: "0.00",
      gradeDetail: '',
    };
  },
  methods: {
    submit() {
      console.log(this.skill)
    },
    // getValue(col, isAura) {
    //   const attr = isAura ? this.skill.auraAttr : this.skill.mainAttr
    //   return attr.getAttr(col.label)
    // },
    elementChanged() {
      this.grade = this.skill.calcGrade()
      this.totalGrade = this.grade.totalGrade.toFixed(2)
    },
    clickGrade() {
      this.gradeDetail = this.grade.detail()
      this.bottom = true
    },
    reset() {
      this.skill = new Skill()
      this.elementChanged()
    }
  },
  computed: {
    rows() {
      const rows = attrLayout.map(row => {
        return {
          title: row.title,
          cols: row.cols
              // .filter(col => {
              //   return this.skill.base.classType != '无' || !config[col].mainClass
              // })
              .map(col => {
                const colConfig = config[col]
                return {
                  ...colConfig,
                  label: col,
                  suffix: colConfig.pct ? '%' : '',
                  defValue: config.defValue(col)
                }
          })
        }
      })
      return rows
    },
  },
}
</script>

