import Vue from "vue";
import Vuex from 'vuex'
import {requestData} from "@/utils/DataRequestUtil";
import {defaultVersion, searchVersion} from "@/config/version-config";
import {bindSkillAndExt} from "@/utils/SourceDataUtil";

Vue.use(Vuex);

function createSuitIndex(SuitSource) {
    const suitIndex = {};
    Object.values(SuitSource).forEach(suit => {
        suitIndex[suit.suitKey] = suit;
    });
    return suitIndex;
}

function createSkillConfigIndex(SkillConfigSource) {
    const skillConfigIndex = {};
    SkillConfigSource.forEach(skillConfig => {
        if (skillConfigIndex[skillConfig['skillId']] == undefined) {
            skillConfigIndex[skillConfig['skillId']] = [];
        }
        skillConfigIndex[skillConfig['skillId']].push(skillConfig);
    });
    return skillConfigIndex;
}

function createTitleIndex(TitleSource) {
    const titleIndex = {};
    TitleSource.forEach(title => {
        titleIndex[title.skillId] = title;
    });
    return titleIndex;
}

export default new Vuex.Store({
    state: {
        CurVersion: defaultVersion,
        LoadingCount: 0,

        HeroSource: {},
        ItemSource: {},
        SkillSource: {},
        SuitSource: {},
        SkillConfigSource: [],
        SkillExtConfigSource: {},
        TitleConfigSource: [],
        EventSource: {},
        Const: {},

        SuitIndex: {},
        SkillConfigIndex: {},
        TitleIndex: {},
    },
    getters: {
        isReady(state) {
            return state.LoadingCount === 0;
        },
    },
    mutations: {
        resetLoading(state) {
            state.LoadingCount = 9;
        },
        loadingOne(state) {
            state.LoadingCount--;
        },
        setVersion(state, version) {
            state.CurVersion = searchVersion(version);
        },
        setHeroSource(state, data) {
            state.HeroSource = data;
        },
        setItemSource(state, data) {
            state.ItemSource = data
        },
        setSkillSource(state, data) {
            state.SkillSource = data
        },
        setSuitSource(state, data) {
            state.SuitSource = data
            state.SuitIndex = createSuitIndex(data)
        },
        setSkillConfigSource(state, data) {
            state.SkillConfigSource = data
            state.SkillConfigIndex = createSkillConfigIndex(data)
        },
        setTitleConfigSource(state, data) {
            state.TitleConfigSource = data
            state.TitleIndex = createTitleIndex(data)
        },
        setSkillExtConfigSource(state, data) {
            state.SkillExtConfigSource = data
        },
        setEventSource(state, data) {
            state.EventSource = data
        },
        setConst(state, data) {
            state.Const = data
        },
        bindSkillExt() {
            bindSkillAndExt()
        }
    },
    actions: {
        initStore({commit, state}) {
            return new Promise(() => {
                commit('resetLoading');
            }).then(Promise.all([
                requestData('hero', state.CurVersion).then(data => commit('setHeroSource', data)).then(() => commit('loadingOne')),
                requestData("item", state.CurVersion).then(res => commit("setItemSource", res)).then(() => commit('loadingOne')),
                requestData("skill", state.CurVersion).then(res => commit("setSkillSource", res)).then(() => commit('loadingOne')),
                requestData("suit", state.CurVersion).then(res => commit("setSuitSource", res)).then(() => commit('loadingOne')),
                requestData("skill_config", state.CurVersion).then(res => commit("setSkillConfigSource", res)).then(() => commit('loadingOne')),
                requestData("skill_ext", state.CurVersion).then(res => commit("setSkillExtConfigSource", res)).then(() => commit('loadingOne')),
                requestData("title_config", state.CurVersion).then(res => commit("setTitleConfigSource", res)).then(() => commit('loadingOne')),
                requestData("event", state.CurVersion).then(res => commit("setEventSource", res)).then(() => commit('loadingOne')),
                requestData("const", state.CurVersion).then(res => commit("setConst", res)).then(() => commit('loadingOne')),
            ]).then(() => {
                commit('bindSkillExt')
            }));
        }
    }
})